/* 引入 vue-swipe 的样式表 */
.mint-swipe[data-v-7c7cfdee],
.mint-swipe-items-wrap[data-v-7c7cfdee] {
  overflow: hidden;
  position: relative;
  height: 100%
}
.mint-swipe-items-wrap[data-v-7c7cfdee] {
  -webkit-transform: translateZ(0);
  transform: translateZ(0)
}
.mint-swipe-items-wrap>div[data-v-7c7cfdee] {
  position: absolute;
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
  width: 100%;
  height: 100%;
  display: none
}
.mint-swipe-items-wrap>div.is-active[data-v-7c7cfdee] {
  display: block;
  -webkit-transform: none;
  transform: none
}
.mint-swipe-indicators[data-v-7c7cfdee] {
  position: absolute;
  bottom: 10px;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%)
}
.mint-swipe-indicator[data-v-7c7cfdee] {
  width: 8px;
  height: 8px;
  display: inline-block;
  border-radius: 100%;
  background: #000;
  opacity: .2;
  margin: 0 3px
}
.mint-swipe-indicator.is-active[data-v-7c7cfdee] {
  background: #fff
}
